import React from 'react';
import { BrowserRouter, Switch, Route, Link } from 'react-router-dom';
import { PageTransition } from '@steveeeie/react-page-transition';
import './index.scss';
import backArrow from './images/back.svg';
import exit from './images/exit.svg'
import kickboard from './images/kickboard.svg';
import chargePort from './images/chargePort.svg';


const result = window.result;
const id = window.station_id;
const name = window.station_name;
const port = window.station_port;
const port_code = window.port_code;
const company = window.company_name;
const key = window.api_key;
const msg = window.msg;

const io = require("socket.io-client");

class Clock extends React.Component{
  constructor(props){
    super(props);
    this.state={
      time: this.props.time,
    }
    
    this.decreaseTime = this.decreaseTime.bind(this);
  }

  decreaseTime(){
    const time = this.state.time;

    if(time > 0){
      this.setState({
        time: time - 1,
      });
    }
    
    else{
      clearInterval(this.timeID);
    }
  }

  componentDidMount(){
    this.timeID = setInterval(this.decreaseTime, 1000);
  }

  componentDidUpdate(){
    if(this.state.time === 0){
      if(this.props.type === 'next'){
        const success = document.querySelector('.success');
        success.click();
      }

      else if(this.props.type === 'back'){
        const ready = document.querySelector('.ready');
        ready.click();
      }
    }
  }

  render(){
    return(
      <div className='clockContainer'>
        <div className='clock'>
          {this.state.time}
        </div>
        <div className='clockText'>
          초 후에 자동으로 화면이 넘어갑니다.
        </div>
      </div>
    )
  }
}

class Brochure extends React.Component{
  render(){
    return(
      <div className='brochure'>
        <div className='explainContainer'>
          <img className='kickboard' src={kickboard} alt='kickboard'></img>
          <div className='firstExplain'>
            <div style={{fontSize:'13px', fontWeight:'400'}}>01</div>
            <div style={{fontSize:'12px', fontWeight:'700'}}>킥보드를 0번 거치대에</div>
            <div style={{fontSize:'12px', fontWeight:'700'}}>끝까지 밀어넣어 주세요.</div>
          </div>
        </div>
        <div className='explainContainer'>
          <img className='chargePort' src={chargePort} alt='chargePort'></img>
          <div className='secondExplain'>
            <div style={{fontSize:'13px', fontWeight:'400'}}>02</div>
            <div style={{fontSize:'12px', fontWeight:'700'}}>단자를 올바르게 꽂아주면</div>
            <div style={{fontSize:'12px', fontWeight:'700'}}>자동으로 충전이 시작됩니다.</div>
          </div>
        </div>
        <div className='split'></div>
      </div>
    )
  }
}

class HeaderText extends React.Component{
  render(){
    return(
      <div className='headerText'>{this.props.text}</div>
    )
  }
}

class HeaderButton extends React.Component{
  render(){
    let imgSource;

    if(this.props.function === 'back'){
      imgSource = backArrow;
    }

    else if(this.props.function === 'exit') {
      imgSource = exit;
    }

    return(
      <img className='headerButton' src={imgSource} alt='headerbutton'></img>
    )
  }
}

class Header extends React.Component{
  render(){
    return(
      <div className='header'>
        <HeaderButton function={this.props.function}></HeaderButton>
        <HeaderText text={this.props.headerText}></HeaderText>
      </div>
    )
  }
}

class ChargeReady extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      headerText: '충전 준비',
    }
  }
  render(){
    return( 
      <div className='page chargeReady'>
        <Header headerText={this.state.headerText} function='back'></Header>
        <Brochure></Brochure>
        <Clock type='next' time={3}></Clock>
      </div>
    )
  }
}

class FooterButton extends React.Component{
  render(){
    return(
      <div className='footerButtonContainer'>
        <button className='footerButton'>{this.props.text}</button>
      </div>
    )
  }
}

class ChargeDisplayPlug extends React.Component{
  render(){
    return(
      <div className='plugContainer'>
        <div>
          <div>
            <div></div>
          </div>
        </div>
      </div>
    )
  }
}

class ChargeDisplayText extends React.Component{
  render(){

    const style1 = {
      fontSize: '18px',
      fontWeight: '700',
      color: '#FF9700',
      marginBottom: '7px',
    }

    const style2 = {
      fontSize: '16px',
      fontWeight: '500',
      color: '#333333',
    }

    const style3 = {
      fontSize: '16px',
      fontWeight: '500',
      color: '#333333',
      marginBottom: '25px',
    }

    return(
      <React.Fragment>
        <div style={style1}>1번 포트</div>
        <div style={style2}>충전이 정상적으로</div>
        <div style={style3}>시작되었습니다.</div>
      </React.Fragment>
    )
  }
}

class ChargeDisplayContainer extends React.Component{
  render(){
    return(
      <div className='chargeDisplayContainer'>
        <ChargeDisplayText></ChargeDisplayText>
        <ChargeDisplayPlug></ChargeDisplayPlug>
      </div>
    )
  }
}

class ChargeSuccess extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      headerText: '충전 성공',
    }
  }

  render(){
    return(
      <div className='page chargeSuccess'>
        <Header headerText={this.state.headerText} function='back'></Header>
        <ChargeDisplayContainer/>
        <Clock type='back' time={5}></Clock>
        <FooterButton text='종료하기'></FooterButton>
      </div>
    )
  }
}

class ErrorText extends React.Component{
  render(){
    return(
      <div className='errorTextContainer'>
        <div className='errorText'>
          {this.props.children}
        </div>
      </div>
    )
  }
}

class ErrorDisplayContainer extends React.Component{

  displayPlug(){
    if(this.props.havePlug === 'true'){
      return <ChargeDisplayPlug></ChargeDisplayPlug>;
    }
    else if(this.props.havePlug === 'false'){
      return null;
    }
    return null;
  }

  render(){
    return(
      <div className='errorDisplayContainer'>
        <div className='errorHeadText'>오류발생</div>
        <ErrorText>{this.props.children}</ErrorText>
        {this.displayPlug()}
      </div>
    ) 
  }
}

class ErrorPage extends React.Component{
  render(){
    return(
      <div className='page errorPage'>
        <Header headerText='' function='exit'></Header>
        <ErrorDisplayContainer havePlug={this.props.havePlug}>
          {this.props.children}
        </ErrorDisplayContainer>
        <FooterButton text='돌아가기'></FooterButton>
      </div>   
    )
  }
}


const Links = () => (
  <div className='nav'>
    <Link to="/" className='ready'>Home</Link>
    <Link to="/success" className='success'>Success</Link>
    <Link to="/errorPage1" replace>ErrorPage1</Link>
    <Link to="/errorPage2" replace>ErrorPage2</Link>
    <Link to="/errorPage3" replace>ErrorPage3</Link>
    <Link to="/errorPage4" replace>ErrorPage4</Link>
  </div>
);
 
class App extends React.Component {

  constructor(props){
    super(props);

  }

  componentDidMount(){
    if(!result){
      window.Android.charge_fail(name, port, 992, msg);
      return false;
    }

    const socket = io();

    let user_data = {"api": key, "type": "company"};

    socket.emit('login', user_data);

    socket.on('result', (data) => {
      //j_data = JSON.parse(data);
      let result = data.data;
      switch(data.code){
        case 'port_ready':
            if(result){
                // $('#status').text('충전 준비 완료');
            } else {
                console.log(data);
                // $('#status').text('충전 준비 실패 : ' + data.detail);
                window.Android.charge_fail(name, port, 992, ''+data.detail);
            }
            break;
        case 'port_cancel':
        if(result){
                // $('#status').text('충전 취소 처리 완료');
            } else {
                console.log(data);
                // $('#status').text('충전 취소 처리 실패 : ' + data.detail);
                window.Android.charge_fail(name, port, 999, "사용자 취소 요청");
            }
            break;
        case 'login':
            if(result){
                // $('#status').text('로그인 성공');
                let station_data = {
                    station_id: id,
                    port_numb: port
                }
                socket.emit('port_ready', station_data);
                // $('#status').text('충전 준비 요청');
            } else {
                // $('#status').text('로그인 실패 : ' + j_data.msg);
            }
            break;
        }
    });
  }

  render(){
    return (
      <BrowserRouter>
        <Links/>
        <Route
          render={({ location }) => {
            return (
              <PageTransition
                preset="moveToLeftFromRight"
                transitionKey={location.pathname}
              >
                <Switch location={location}>
                  <Route exact path="/">
                    <ChargeReady></ChargeReady>  
                  </Route>
                  <Route exact path="/success">
                    <ChargeSuccess></ChargeSuccess>  
                  </Route>
                  <Route exact path="/errorPage1">
                    <ErrorPage havePlug='true'>
                      <div></div>
                      <div>아래 모양의 단자를 가진 충전소에서 충전해주세요.</div>
                    </ErrorPage>
                  </Route>
                  <Route exact path="/errorPage2">
                    <ErrorPage havPlug='false'>
                      <div>현재 이용이 불가능한 충전소입니다.</div>
                      <div>다른 충전소를 이용해주세요.</div>
                    </ErrorPage>
                  </Route>
                  <Route exact path="/errorPage3">
                    <ErrorPage havPlug='false'>
                      <div>충전단자가 정상적으로 인식되지 않았습니다.</div>
                      <div>킥보드에 단자를 올바르게 삽입해주세요.</div>
                    </ErrorPage>
                  </Route>
                  <Route exact path="/errorPage4">
                    <ErrorPage havPlug='false'>
                      <div>충전소 이용을 다시 시도해주세요.</div>
                    </ErrorPage>
                  </Route>
                </Switch>
              </PageTransition>
            );
          }}
        />
      </BrowserRouter>
    );
  }
}

export default App;
